<template>
    <div id="nav">
        <el-menu :default-active="$route.path" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" background-color="#141B22" text-color="#909399" active-text-color="#ffffff" :collapse="isCollapse" :collapse-transition="false" router>
            <el-radio-group v-model="isCollapse" @click.native="collapse()" style="margin-bottom: 20px;">
                <el-radio-button :label="false" v-if="isCollapse"><i class="iconfont icon-jiantou-you-cuxiantiao-fill" style="font-size:20px;"></i></el-radio-button>
                <el-radio-button :label="true" v-else><i class="iconfont icon-jiantou-zuo-cuxiantiao-fill" style="font-size:20px;"></i></el-radio-button>
            </el-radio-group>
            <el-menu-item index="/zgmb">
                <i class="iconfont icon-mubiao"></i>
                <span slot="title">诊改目标体系</span>
            </el-menu-item>
            <el-menu-item index="/zgbz">
                <i class="iconfont icon-cloud-standard"></i>
                <span slot="title">诊改标准体系</span>
            </el-menu-item>
            <el-menu-item index="/zgjx">
                <i class="iconfont icon-jixiao"></i>
                <span slot="title">诊改绩效考核</span>
            </el-menu-item>
        </el-menu>
    </div>
</template>

<script>
import store from '../../store/store.js'
export default {
    data() {
        return {
            isCollapse:true,
        }
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        collapse(){
            console.log(this.isCollapse)
            store.commit('isCollapse',this.isCollapse)
        }
    },
}
</script>

<style>
#nav .el-radio-group{
    /* background: #141B22 */
}
#nav .el-radio-button__inner{
    border: 0!important;
    /* background: #141B22!important; */
}
#nav .el-menu{
    border: 0;
}
#nav .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
}
#nav .el-submenu__title:hover{
    background: #029348!important;
    color: #ffffff!important;
    transition: 0s;
}
#nav .el-submenu__title:hover i{
    color: #ffffff!important;
}
#nav .el-menu-item:focus,#nav .el-menu-item:hover{
    background: #029348!important;
    color: #ffffff!important;
    transition: 0s;
}
#nav .el-menu-item:hover i{
    color: #ffffff!important;
    transition: 0s;
}
#nav .el-radio-button .el-radio-button__inner{
        background:#141B22!important;
        border: 0;
        color: #A1A5AC;
}
#nav .el-menu-item:focus, .el-menu-item:hover{
    background-color: #029348 !important;
    color: #ffffff !important;
}
#nav .el-menu-item .is-active{
    background: #029348!important;
}
#nav .el-menu-vertical-demo .el-menu-item{
    height: 120px!important;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#nav .el-menu-vertical-demo .el-menu-item i{
    font-size: 18px;
    font-weight: 900;
}
#nav .el-menu-vertical-demo .el-menu-item i,#nav .el-menu-vertical-demo .el-menu-item span{
    height: 50px;
    line-height: 50px;
}
#nav .el-menu-item .el-tooltip{
    line-height: 120px;
}
.el-menu-vertical-demo .is-active{
    background: #029348!important;
}
</style>



