<template>
    <div id="header" style="width:100%;height:65px;background:#242f42;position:fixed;z-index:1;">
        <el-row>
            <el-col :span="24">
                <div class="grid-content bg-purple" style="width:35%;">
                    <div class="logo">
                        <img :src="logo" alt="玉带职业技术学校">
                    </div>
                    <div class="title">
                        <p style="letter-spacing:10px;">玉带职业技术学校<p/>
                        <div class="l_title">诊改双链工作管理平台</div>
                    </div>
                </div>
                <div class="grid-content bg-purple-light" style="width:65%;float:right;">
                    <div style="float:right;">
                        <el-input
                            placeholder="请输入搜索内容"
                            prefix-icon="el-icon-search"
                            ref="search"
                            v-model="search"
                            clearable
                            style="width:230px;margin-top:12px;">
                        </el-input>
                        <el-select @change="wech()" v-model="value" placeholder="请选择内容">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                        <el-button 
                        @click="handleAdd"
                        type="primary" 
                        icon="el-icon-search" 
                        style="margin-top:12px;background-color: #029348;border:1px solid #029348;">
                            搜索
                        </el-button>
                    <ul class="headerNav">
                        <li class="userInfo">
                            <div class="info">
                                <span class="name">{{name}}</span>
                                <span class="zw">教职工</span>
                            </div>
                            <div class="tx"><img :src="touxiang" alt=""></div>
                        </li>
                        <li class="loginout">
                            <el-dropdown trigger="click" @command="handleCommand">
                                <span class="el-dropdown-link">
                                    <div class="tuichu">
                                        <i class="iconfont icon-tuichu" style="font-size:20px;"></i>
                                    </div>
                                </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item command="1">诊改数据深度挖掘平台</el-dropdown-item>
                                    <el-dropdown-item command="2">诊改双链工作管理平台</el-dropdown-item>
                                    <el-dropdown-item command="3">诊改在线课堂教学平台</el-dropdown-item>
                                    <el-dropdown-item command="4">诊改校本数据中心</el-dropdown-item>
                                    <el-dropdown-item command="5">诊改大屏全景展示平台</el-dropdown-item>
                                    <el-dropdown-item command="6">退出登录</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </li>
                    </ul>
                    </div>
                </div>
            </el-col>
        </el-row>
        <div class="mask" v-if="mask">
            <i @click="click" class="el-icon-error error"></i>
            <el-table
                :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)"
                class="tableData">
                <el-table-column
                    prop="name"
                    label="姓名"
                    width="230">
                </el-table-column>
                <el-table-column
                    prop="user_id"
                    label="工号"
                    width="260">
                </el-table-column>
                <el-table-column
                    prop="mobile"
                    label="手机号">
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">选择</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="block">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currpage"
                :page-sizes="[5,10,15]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            mask:false,
            logo: 'http://cdn.xqit.net/zg/img/yd2.png',
            touxiang: 'http://cdn.xqit.net/zg/img/laoshi.png',
            search:'',
            name: '',
            options: [{
            value: '目标链',
            label: '目标链'
            }, {
            value: '标准链',
            label: '标准链'
            }, {
            value: '绩效分数',
            label: '绩效分数'
            }],
            value: '',
            tableData: [],
            currpage: 1,
            total:100,
            pagesize:10,
        }
    },
    methods:{
        handleAdd:function(){
            var that = this;
            console.log(this.$refs.search.value)
            var user_id=window.sessionStorage.getItem('user_id')
            $.ajax({
                type: "post",
                url: this.baseUrl+"/ydjxzg/mb/getxsmdsc.action",
                data: {
                    user_id:user_id,
                    name:this.$refs.search.value
                },
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    console.log(res.md[0].user_id)
                    if(res.md.length == 1){
                        $.ajax({
                            type: "post",
                            url: that.baseUrl+"/ydjxzg/mb/getxsscore.action",
                            data: {
                                manager_id:res.md[0].user_id,
                                target_name:""
                            },
                            dataType: "json",
                            success: function (res) {
                                console.log(res)
                                that.$router.push({
                                    name: 'zgjx',
                                    params: {
                                        core: res
                                    }
                                })
                            },
                        });
                    }else if(res.md.length == 0){
                        that.$message.warning("无搜索数据")
                    }else{
                        that.mask = true;
                        that.tableData = res.md;
                        that.total = res.md.length;
                    }
                },
            });
        },
        click(){
            this.mask = false;
        },
        handleCommand(command){
            console.log(command)
            var user=window.sessionStorage.getItem('user')
            var user_id=window.sessionStorage.getItem('user_id')
            var userdj=window.sessionStorage.getItem('userdj')
            var usergender=window.sessionStorage.getItem('usergender')
            var _this = this
            if(command =='1'){
                //window.location.href = 'http://zg.demo.xqit.net:82/#/hxsj'+'?name='+user+'&dj='+userdj+'&gender='+usergender+'&user_id='+user_id
                window.location.href = '/ydjxzg/#/hxsj'+'?name='+user+'&dj='+userdj+'&gender='+usergender+'&user_id='+user_id
            }
            if(command == '6'){
                window.sessionStorage.removeItem('login')
                window.sessionStorage.removeItem('user')
                window.sessionStorage.removeItem('user_id')
                window.sessionStorage.removeItem('userdj')
                window.sessionStorage.removeItem('usergender')
                $.ajax({
                    type: "post",
                    url: _this.baseUrl+"/ydjxzg/log/logout.action",
                    data: {},
                    dataType: "json",
                    success: function (res) {
                        console.log(res)
                        _this.$message.success('您已退出登录')
                        window.location.href = _this.baseUrl+'/ydjxzg/#/login'   //开发
                         //window.location.href = _this.baseUrl+"/ydjxzg/index.html"     //线上
                        
                    },
                });
            }
        },
        wech(){
            console.log(this.value)
            if(this.value == "目标链"){
                this.$router.push({name: "zgmb"});
            }else if(this.value == "标准链"){
                this.$router.push({name: "zgbz"});
            }else{
                this.$router.push({name: "zgjx"});
            }
        },
        handleEdit(index, row) {
            console.log(index, row);
            this.mask = false;
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pagesize = val
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currpage = val
        }
    },
    mounted() {
        var _this = this
        setTimeout(function(){
            _this.name = window.sessionStorage.getItem('user')
        },0)
    },
}
</script>

<style scoped>
.grid-content{
    height: 65px;
    float: left;
    overflow: hidden;
}
.logo{
    width: 65px;
    height: 65px;
    float: left;
    /* margin: 0 20px 0; */
}
.logo img{
    width: 65px;
    height: 65px;
}
.title{
    /* width: 370px; */
    height: 27px;
    float: left;
    font-size: 20px;
    line-height: 27px;
    padding-left: 30px;
    text-align: left;
    color: #fff;
    display: inline-block;
    margin-top: 10px;
}
.l_title{
    float: left;
    font-size: 14px;
    text-align: left;
    color: #fff;
    letter-spacing: 7.5px;
    font-family: 'KaiTi';
}
.headerNav{
    float: right;
    display: flex;
    color: #fff;
}
.userInfo{
    width: 80px;
    height: 65px;
    padding: 0 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
}
.info{
    width: 42px;
    height: 36px;
    float: left;
    line-height: 18px;
    margin-right: 5px;
}
.name{
    display: block;
    text-align: right;
    font-size: 14px;
}
.zw{
    color: rgb(186, 186, 186);
    font-size: 12px;
    float: right;
    text-align: right;
}
.tx{
    width: 30px;
    height: 30px;
    float: right;
}
.tx img{
    width: 100%;
    height: 100%;
}
.loginout{
    line-height: 65px;
    padding: 0 20px;
    cursor: pointer;
}
.tuichu{
    color: #ffffff;
}
.mask{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 99;
}
.tableData{
    width: 60%;
    margin: auto;
    margin-top: 10%;
}
.error{
    font-size: 30px;
    color: #ffffff;
    position: fixed;
    right: 0;
    top: 0;
    margin: 100px 100px 0 0;
}
.block{
    margin-top: 10px;
    float: right;
    margin-right: 20%;
}
</style>



