<template>
    <div id="main">
        <el-container>
            <el-header>
                <v-header></v-header>
            </el-header>
            <el-main>
                <div class="aside">
                    <v-nav></v-nav>
                </div>
                <div class="content">
                    <div class="view">
                        <keep-alive v-if="$route.meta.keepAlive">
                            <router-view></router-view>
                        </keep-alive>
                        <keep-alive v-if="!$route.meta.keepAlive">
                            <router-view></router-view>
                        </keep-alive>
                    </div>
                    <v-footer></v-footer>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
import Header from './home/header.vue';
import nav from './home/nav.vue';
import footer from './home/footer.vue';
export default {
    name: 'Main',
    data(){
        return{
            
        }
    },
    components:{
        'v-header': Header,
        'v-nav': nav,
        'v-footer': footer,
    },
    methods: {
        scroll(){
            var Content = document.getElementsByClassName('content')
            // console.log(Content)
            Content[0].scrollTop = 0
        }
    },
    mounted() {
        var View = document.getElementsByClassName('view')
        var windowHeight = document.documentElement.offsetHeight;
        // console.log(View,windowHeight)
        View[0].style.minHeight = windowHeight - 160 + 'px'
    },
    watch: {
        '$route':{
            handler(route){
                var _this = this
                // console.log(route,_this)
                if(route.name != ''){
                    _this.scroll()
                }
            },
            deep: true
        }
    },
}
</script>

<style scoped>
#main{
    width: 100%;
    height: 100%;
}
.el-header{
    width: 100%;
    height: 65px!important;
    background: #242f42;
    padding: 0;
}
.el-container{
    width: 100%;
    height: 100%;
    display: flex;
}
.el-main{
    padding: 0;
    display: flex;
}
.aside{
    /* height: 100%; */
    background: #1C2228;
    float: left;
}
.content{
    overflow-y: scroll;
    overflow-x: hidden;
    flex: 1;
}
.view{
    /* min-height: 86%; */
    margin: 0 2% 50px;
    /* overflow: hidden; */
    position: relative;
}
</style>



